import React, { useMemo, useState } from 'react';
import { BarChart3, Filter } from 'lucide-react';

interface PerformanceRow {
  studentId: string;
  name: string;
  participation: number;
  project: number;
  quiz: number;
  notes?: string;
}

const StudentPerformance: React.FC = () => {
  const [classId, setClassId] = useState('CLS-101');
  const [rows, setRows] = useState<PerformanceRow[]>(
    Array.from({ length: 10 }).map((_, i) => ({
      studentId: `22010${(i + 1).toString().padStart(3, '0')}`,
      name: `Student ${i + 1}`,
      participation: Math.round(60 + Math.random() * 40),
      project: Math.round(60 + Math.random() * 40),
      quiz: Math.round(60 + Math.random() * 40),
      notes: '',
    }))
  );

  const averages = useMemo(() => {
    if (rows.length === 0) return { participation: 0, project: 0, quiz: 0 };
    const sum = rows.reduce(
      (acc, r) => ({
        participation: acc.participation + r.participation,
        project: acc.project + r.project,
        quiz: acc.quiz + r.quiz,
      }),
      { participation: 0, project: 0, quiz: 0 }
    );
    return {
      participation: Math.round((sum.participation / rows.length) * 10) / 10,
      project: Math.round((sum.project / rows.length) * 10) / 10,
      quiz: Math.round((sum.quiz / rows.length) * 10) / 10,
    };
  }, [rows]);

  const updateRow = (idx: number, key: keyof PerformanceRow, value: string | number) => {
    setRows(prev => prev.map((r, i) => (i === idx ? { ...r, [key]: value as never } : r)));
  };

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Student Performance</h1>
        <div className="text-sm text-gray-500">View and input performance indicators</div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="card lg:col-span-2">
          <div className="flex items-center justify-between mb-4">
            <div>
              <div className="font-semibold text-gray-900">Class</div>
              <div className="text-sm text-gray-500">Select a class</div>
            </div>
            <select className="border rounded-lg px-3 py-2" value={classId} onChange={(e) => setClassId(e.target.value)}>
              <option value="CLS-101">Database Systems (CS101)</option>
              <option value="CLS-202">Advanced Algorithms (CS202)</option>
            </select>
          </div>

          <div className="overflow-x-auto">
            <table className="min-w-full text-sm">
              <thead>
                <tr className="text-left text-gray-500">
                  <th className="py-2 pr-4">Student ID</th>
                  <th className="py-2 pr-4">Name</th>
                  <th className="py-2 pr-4">Participation</th>
                  <th className="py-2 pr-4">Project</th>
                  <th className="py-2 pr-4">Quiz</th>
                  <th className="py-2 pr-4">Notes</th>
                </tr>
              </thead>
              <tbody>
                {rows.map((r, idx) => (
                  <tr key={r.studentId} className="border-t">
                    <td className="py-2 pr-4">{r.studentId}</td>
                    <td className="py-2 pr-4">{r.name}</td>
                    {(['participation', 'project', 'quiz'] as const).map((k) => (
                      <td key={k} className="py-2 pr-4">
                        <input
                          type="number"
                          min={0}
                          max={100}
                          className="w-24 border rounded px-2 py-1"
                          value={r[k]}
                          onChange={(e) => updateRow(idx, k, Number(e.target.value))}
                        />
                      </td>
                    ))}
                    <td className="py-2 pr-4">
                      <input
                        className="w-56 border rounded px-2 py-1"
                        value={r.notes}
                        onChange={(e) => updateRow(idx, 'notes', e.target.value)}
                        placeholder="optional"
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        <div className="space-y-6">
          <div className="card">
            <div className="font-semibold text-gray-900 mb-2 inline-flex items-center">
              <BarChart3 className="h-4 w-4 mr-2" /> Class Averages
            </div>
            <div className="text-sm text-gray-700 space-y-1">
              <div>Participation: <span className="font-medium text-gray-900">{averages.participation}</span></div>
              <div>Project: <span className="font-medium text-gray-900">{averages.project}</span></div>
              <div>Quiz: <span className="font-medium text-gray-900">{averages.quiz}</span></div>
            </div>
          </div>

          <div className="card">
            <div className="font-semibold text-gray-900 mb-2 inline-flex items-center">
              <Filter className="h-4 w-4 mr-2" /> Filters
            </div>
            <div className="text-sm text-gray-600">More filters can be added later (e.g., min score, missing data)</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StudentPerformance;
